/*
Стили самой карточки
*/
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/*
Стили изображения
*/
.image {
  max-width: 400px;
}

/*
Название
*/
.title {
  font-size: 25px;
  color: #393a34;
  font-family: 'Helvetica', Arial, sans-serif;
  margin: 5px 0;
}

/*
Дата появления
*/
.description {
  font-size: 14px;
  font-style: italic;
  color: #393a34;
  font-family: 'Helvetica', Arial, sans-serif;
  margin: 5px 0;
} 